.essentials {
	margin-bottom: 30px;
	.image_log {
		width: calc(100% - 40px);
		height: 40vh;
		margin: 20px;
	}
	.search_container {
		background: rgba(5, 119, 123, 0.4);
		padding: 20px;
		.search_bar {
			display: inline-block;
		}
		input {
			margin-left: 30vw;
			padding: 5px;
			width: 400px;
			background: #ffffff;
			border-radius: 12px;
		}
		.back {
			background: #06979d;
			border-radius: 11px;
			padding: 5px;
			float: left;
			color: white;
			cursor: pointer;
			padding-left: 10px;
			padding-right: 10px;
			span {
				margin-left: 10px;
			}
		}
		.cart {
			background: #06979d;
			border-radius: 11px;
			padding: 5px;
			float: right;
			span {
				display: inline-block;
				color: white;
				margin-right: 10px;
			}
			img {
				display: inline-block;
			}
		}
	}
	.sorting {
		padding: 10px;
		.sort_by {
			float: right;
			padding-right: 20px;
			span {
				margin-right: 15px;
			}
			select {
				background: rgba(5, 119, 123, 0.4);
				border: none;
				outline: none;
				padding: 5px;
				border-radius: 5px;
			}
		}
	}
	.essentials_list {
		margin-left: 20px;
		.card {
			background: #faf7f7;
			border: 1px solid #d7d7d7;
			box-sizing: border-box;
			border-radius: 14px;
			margin: 10px;
			padding: 10px;
			.name {
				font-family: HelveticaNeue;
				font-style: normal;
				font-weight: normal;
				font-size: 20px;

				color: #000000;
				margin-bottom: 10px;
			}
			.rating {
				.button {
					float: right;
					background: #df0000;
					border-radius: 5px;
					color: white;
					padding: 5px;
				}
				.no_of_reviews {
					font-family: HelveticaNeue;
					font-style: normal;
					font-weight: 300;
					font-size: 12px;
					/* identical to box height */

					color: #000000;
				}
			}
			.price {
				margin-top: 10px;
				margin-bottom: 10px;
				font-family: HelveticaNeue;
				font-style: normal;
				font-weight: 300;
				font-size: 10px;
				line-height: 12px;

				color: #000000;
			}
			.price1 {
				float: right;
				display: inline-block;
				font-family: HelveticaNeue;
				font-style: normal;
				font-weight: bold;
				font-size: 10px;
				line-height: 12px;

				color: #06979d;
			}
			img {
				margin: 10px;
				margin: auto;
			}
		}
	}
	.product_info {
		.image_log {
			object-fit: contain;
			width: calc(100% - 40px);
			margin: 20px;
			border-radius: 10px;
			border: 1px solid #c6c6c6;
		}
		.price {
			margin-top: 10px;
			font-family: HelveticaNeue;
			font-style: normal;
			font-weight: bold;
			font-size: 17px;
			line-height: 33px;

			color: #000000;
		}
		.available {
			margin-top: 50px;
			font-family: HelveticaNeue;
			font-style: normal;
			font-weight: bold;
			font-size: 16px;
			line-height: 19px;

			color: #000000;
			margin-bottom: 10px;
		}
		.delivery {
			background: rgba(5, 119, 123, 0.24);
			display: inline-block;
			padding: 5px;
			padding-right: 20px;
			border-radius: 5px;
			span {
				color: blue;
			}
		}
		.cart_button {
			margin-top: 20px;
		}
		.buy_now {
			background: #df0000;
			border-radius: 10px;
			padding: 10px;
			color: white;
		}
		.add_to_cart {
			margin-right: 20px;
			color: #df0000;
			background: #ffffff;
			box-shadow: 1px 1px 6px rgba(0, 0, 0, 0.25);
			border-radius: 10px;
			padding: 10px;
		}
		input {
			background: #ffffff;
			border: 1px solid #cdcdcd;
			box-sizing: border-box;
			border-radius: 5px;
			margin-left: 10px;
			text-align: center;
		}
		.overview {
			width: 100%;
			margin: 0px;
			background: #faf7f7;
		}
	}
	.heading_lower {
		display: inline-block;
		font-family: HelveticaNeue;
		font-style: normal;
		font-weight: 600;
		font-size: 15px;
		line-height: 29px;
		color: #000000;
		margin-left: 20px;
	}
	.margin_top {
		margin-left: 0px;
		margin-top: 15px;
		font-size: 20px;
	}
	.margin_left {
		margin-left: 40px;
	}
}
