.intern {
	.image {
		border-radius: 10px;
		width: calc(100% - 40px);
		height: 40vh;
		margin: 20px;
	}
	.content {
		h4 {
			margin: 20px;
		    font-weight: 600;
		    font-size: 1.6em; 	
  		}
  		h3, h1 {
			margin: 20px;
		    font-weight: 700;
		    text-align: left;
  		}
	  	.line {
	  		margin-left: 20px;
			margin-top: 15px;
			margin-bottom: 15px;		
			width: 200px;
			background: #000000;
			height: 1px;
		}
		.form {
			width: 40%;
			label {
	  			margin-left: 20px;
				width: 100%;
			}
			input {
				display: block;
	  			margin-left: 20px;
				width: 100%;
				background: #FFFFFF;
				border: 1px solid #958888;
				box-sizing: border-box;
				border-radius: 7px;
			}
		}
	}
	.footer {
		border: none;
		input {
			display: none;
		}
		label {
	  		margin-left: 10px;
			font-size: 14px;
			padding: 5px;
			color: white;
			background: #06979D;
			border-radius: 6px;
		}
	}
	.intern_button {
		margin-left: 40%;
		.intern {
			width: 150px;
			color: white;
			margin-right: 10px;
			padding: 5px;
			background: #06979D;
			border-radius: 10px;
		}
		.full_time {
			width: 150px;
			color: #06979D;
			padding: 5px;
			background: #FFFFFF;
			border: 1px solid #06979D;
			box-sizing: border-box;
			border-radius: 10px;
		}
	}
	h5 {
		margin: 20px;
	    font-weight: 700;
	    text-align: left;
	}
	.check {
		margin-left: 20px;
		.checkboxes1 {
			label {
				margin-left: 10px;
			}
			margin-right: 30px;
		}
		.checkboxes2 {
			label {
				margin-left: 10px;
			}
			margin-right: 20px;
		}
	}
	.footer2 {
		margin-bottom: 40px;
		margin-left: 20px;
		margin-top: 20px;
		.submit {
			color: white;
			width: 140px;
			padding: 5px;
			background: #DF0000;
			border-radius: 5px;
		}
	}
}